<template>
    <div class="teacher-main">
        <header class="header">
            <span @click="btnGo" class="btnGo"></span>
            {{text}}
            <div class="btn-ng">
                <span></span>
                <span></span>
                <span></span>
            </div>
        </header>
    </div>
</template>
<script>
    export default {
        name : 'header',
        props:{
            text:''
        },
        methods:{
            btnGo:function(){
                this.$router.go(-1);
            }
        }
    }
</script>
<style lang="less" scoped>
    .header {
        width: 100%;
        height: .62rem;
        background: -webkit-linear-gradient(left, #f97522, #fd5148);
        /* Safari 5.1 - 6.0 */
        background: -o-linear-gradient(right, #f97522, #fd5148);
        /* Opera 11.1 - 12.0 */
        background: -moz-linear-gradient(right, #f97522, #fd5148);
        /* Firefox 3.6 - 15 */
        background: linear-gradient(to right, #f97522, #fd5148);
        /* 标准的语法 */
        padding: .16rem 0;
        overflow: hidden;
        text-align:center;
        position:relative;
        color:#fff;
        font-size:.36rem;
        font-weight:bold;
        line-height:.62rem;
        .btnGo{
            position:absolute;
            left:.2rem;
            top:50%;
            margin-top:-.23rem;
            width:.46rem;
            height:.46rem;
            background:url(../../../assets/btn-return.png) no-repeat;
            background-size:100% 100%;
        }
        .btn-ng{
            height:100%;
            position:absolute;
            top:50%;
            right:.2rem;
            margin-top:-.31rem;
            span{
                display:inline-block;
                width:.06rem;
                height:.06rem;
                border:.06rem solid #fff;
                border-radius:50%;
            }
        }
    }
</style>


